import { Link } from 'gatsby';
import React from 'react';
import { motion, useScroll } from 'framer-motion';

const Header = ({ isPost }) => {
  const { scrollYProgress } = useScroll();
  return (
    <div className='sticky top-0 bg-white z-30  flex flex-col items-start justify-start gap-y-0 h-full w-full mb-4'>
      <header className='header' id='header'>
        <nav className='nav-container' id='header-container'>
          <Link to='/' id='blog-title' className='cursor-pointer'>
            <img
              src='../../hk-blog-icon.png'
              alt='blog-logo'
              className='max-h-14 w-full max-w-[3.5rem]'
            />
          </Link>
          <div className='flex justify-between cursor-pointer items-center'>
            <Link to='/hankyeolk' className='hover-text-gray px-2 py-1'>
              누구세요?
            </Link>
          </div>
        </nav>
      </header>
      {isPost && (
        <motion.div
          className='sticky w-full bottom-0 left-0 right-0 h-4 bg-[#e599f7] bg-opacity-70 origin-[0%]'
          style={{ scaleX: scrollYProgress }}
        />
      )}
    </div>
  );
};

export default Header;
