import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { Helmet } from 'react-helmet';

const Seo = ({ description, title }) => {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            og_img
            author {
              name
              enName
            }
          }
        }
      }
    `
  );
  const postDescription = description || site.siteMetadata.description;

  return (
    <Helmet
      title={title}
      defaultTitle={site.siteMetadata.title}
      meta={[
        { property: `og:title`, content: title },
        { property: `og:site_title`, content: site.siteMetadata.title },
        { property: `og:description`, content: postDescription },
        { property: `og:author`, content: site.siteMetadata.author.name },
        { property: `og:en_author`, content: site.siteMetadata.author.enName },
        { property: `og:image`, content: site.siteMetadata.og_img }
      ]}
    />
  );
};

export default Seo;
